.grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.grid-no-align {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
