:root {
    --primary-color: #047aed;
    --secondary-color: #1c3fa8;
    --dark-color: #002240;
    --light-color: #f4f4f4;
    --success-color: #5cb85c;
    --error-color: #d9534f;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    font-family: "Lato", sans-serif;
    color: #333;
    line-height: 1.6;
}

input, select, textarea, button{font-family:inherit;}

form {
    max-width: 500px;
}

form [for="searchbar"] {
    position: relative;
}

label[for="searchbar"] i {
    position: absolute;
    right: 10px;
    top: 2px;
    color: #047aed;
    color: var(--primary-color)
} 

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: #333;
}

h1, h2 {
    font-weight: 300;
    line-height: 1.2;
}

.navbar {
    background-color: #047aed;
    background-color: var(--primary-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
}

.navbar ul {
    display: flex;
}

.navbar a {
    color: #fff;
    padding: 10px;
    margin: 0 5px;
}

.navbar .flex {
    justify-content: space-between;
}

/* Image Loader */

.img-loader {
    position: relative;
}

.img-loading {
    position: absolute;
    height: 100px;
    width: 100px;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
}

.userContainer {
    position: relative;
}

.userContainer .userMenu {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: #047aed;
    color: var(--primary-color);
    background-color: #fff;
    padding: 0 10px;
    top: -900px;
    right: 10px;
    border-radius: 5px;
    border: 1px solid gray;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
    z-index: -1;
}

#user-link {
    display: block;
    margin-right: 10px;
    cursor: pointer;
}

/* #user-link:hover+.userMenu {
    animation: mouse-in .4s linear;
    animation-fill-mode: forwards;
} */

.show-user-menu{
    -webkit-animation: mouse-in .4s linear;
            animation: mouse-in .4s linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}


/* .userMenu:hover {
    top: 30px;
    z-index: 1;
} */

.userMenu li {
    display: flex;
    align-items: center;
    margin: 5px;
    cursor: pointer;
}

.userMenu li i {
    margin-right: 3px;
}

.profile-dropdown {
    cursor: pointer;
}

/*Radio styling for Payment Screen */

.radio-input {
    padding: 20px;
}

/* Checkout Screen */

.pricing-breakdown {
    min-width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.pricing-breakdown p {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/*Footer */

.social-links {
    margin: 0 auto;
    min-width: 200px;
    display: flex;
    justify-content: space-between;
}

.calc-height {
    min-height: calc(100vh - 142px);
}

/*Custom grid layouts*/

.grid-5 {
    grid-template-columns: 2fr 1fr 1fr .2fr .2fr;
    grid-gap: 10px;
    gap: 10px;
    justify-items: center;
}

.grid-offset {
    grid-template-columns: 3fr 1fr;
}

/*Order History Table*/


#order {
    text-align: center;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
}

#order td, #order th {
    border: 1px solid #ddd;
    padding: 8px;
}

#order tr:nth-child(odd) {
    background-color: #f2f2f2;
}


#order th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #047aed;
    background-color: var(--primary-color);
    color: white;
}

/* Toggle styles */
.toggle-container {
    display: flex;
    align-items: center;
    margin: 15px 0 20px 0;
}

.toggle {
    display: none;
}

.toggle-container .label {
    position: relative;
    background-color: #d0d0d0;
    border-radius: 50px;
    margin-right: 10px;
    cursor: pointer;
    display: inline-block;
    width: 60px;
    height: 30px;
}

.toggle:checked + .label {
    background-color: #047aed;
    background-color: var(--primary-color);
}

.ball {
    background: #fff;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    position: absolute;
    top: 3px;
    left: 3px;
    align-items: center;
    justify-content: center;
    -webkit-animation: slideOff 0.3s linear forwards;
            animation: slideOff 0.3s linear forwards;
}

.toggle:checked + .label .ball {
    -webkit-animation: slideOn 0.3s linear forwards;
            animation: slideOn 0.3s linear forwards;
}

/* For the Custom Carousel */
.carousel-container {

    /* background-color: black; */
}

.carousel-inner {
    position: relative;
    display: flex;
    height: 400px;
    width: 600px;
    max-width: 90vw;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    transition: .3s linear;
}

.carousel-inner .left,
.carousel-inner .right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 5% 1;
  height: 100%;
}

.carousel-inner .center {
    position: relative;
    flex: 90% 1;
    height: 100%;
    display: flex;
    /*AFTER implementation of setInterval */
    flex-direction: column-reverse;
    justify-content: end;
    align-items: center;
}

.carousel-inner .left i,
.carousel-inner .right i,
.center i {
    cursor: pointer;
    color: rgba(131, 123, 123, 0.705);
    opacity: .8;
}

.carousel-inner .left i:active,
.carousel-inner .right i:active {
    transform: scale(.97)
}

.center i.fa-play {
    position: absolute;
    bottom: 60px;
    left: 50%;
    transform: translateX(-50%);
}

/* End Custom Carousel */

/* Toggle Animations start */

@-webkit-keyframes slideOn {
    0% {
        transform: translateX(0) scale(1);
    }

    50% {
        transform: translateX(20px) scale(1.1);
    }
    100% {
        transform: translateX(30px) scale(1);
    }
}

@keyframes slideOn {
    0% {
        transform: translateX(0) scale(1);
    }

    50% {
        transform: translateX(20px) scale(1.1);
    }
    100% {
        transform: translateX(30px) scale(1);
    }
}

@-webkit-keyframes slideOff {
    0% {
        transform: translateX(30px) scale(1);
    }

    50% {
        transform: translateX(20px) scale(1.1);
    }
    100% {
        transform: translateX(0px) scale(1);
    }
}

@keyframes slideOff {
    0% {
        transform: translateX(30px) scale(1);
    }

    50% {
        transform: translateX(20px) scale(1.1);
    }
    100% {
        transform: translateX(0px) scale(1);
    }
}

/* User Menu Animations */


@-webkit-keyframes mouse-in {
    25% {
        top: -5px;
        z-index: -1;
        opacity: .2;
        transform: scale(.3);
    }
    50% {
        top: 60px;
        z-index: -1;
        opacity: .4;
        transform: scale(.4);
    }
    75% {
        top: 45px;
        opacity: .6;
        transform: scale(.75);
    }
    100% {
        top: 30px;
        z-index: 2;
        opacity: 1;
        transform: scale(1);
    }
}


@keyframes mouse-in {
    25% {
        top: -5px;
        z-index: -1;
        opacity: .2;
        transform: scale(.3);
    }
    50% {
        top: 60px;
        z-index: -1;
        opacity: .4;
        transform: scale(.4);
    }
    75% {
        top: 45px;
        opacity: .6;
        transform: scale(.75);
    }
    100% {
        top: 30px;
        z-index: 2;
        opacity: 1;
        transform: scale(1);
    }
}

@-webkit-keyframes mouse-out {
    
    25% {
        top: 30px;
        z-index: 2;
        opacity: 1;
        transform: scale(1);
    }
    50% {
        top: 45px;
        opacity: .6;
        transform: scale(.75);
    }
    75% {
        top: 60px;
        z-index: -1;
        opacity: .4;
        transform: scale(.4);
    }
    100% {
        top: -5px;
        z-index: -1;
        opacity: .2;
        transform: scale(.3);
    }
    
}

@keyframes mouse-out {
    
    25% {
        top: 30px;
        z-index: 2;
        opacity: 1;
        transform: scale(1);
    }
    50% {
        top: 45px;
        opacity: .6;
        transform: scale(.75);
    }
    75% {
        top: 60px;
        z-index: -1;
        opacity: .4;
        transform: scale(.4);
    }
    100% {
        top: -5px;
        z-index: -1;
        opacity: .2;
        transform: scale(.3);
    }
    
}



/*Tables and under*/

@media(max-width: 768px) {
    .grid {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
    .grid-5 {
        grid-template-columns: 2fr 1fr 1fr .2fr .2fr;
    }
    .navbar .flex {
        flex-direction: column;
    }
    .navbar ul {
        padding: 10px;
        background-color: rgba(0, 0, 0, 0.1);
        border-radius: 5px;
    }
    .calc-height {
        min-height: calc(100vh - 242px);
    }

    form.m-1 {
        width: 347px;
    }
}
